html {
  margin: 0;
  padding: 0;
}

body {
  width: 100%;
  height: 100%;
  max-height: 100%;
  box-sizing: border-box;
  overscroll-behavior: none;
  margin: 0;
  padding: 20px;
  position: relative;
  overflow: hidden;
}

@media (orientation: landscape) {
  #surfer-container-width-sliders {
    height: 100%;
    box-sizing: content-box;
    grid-template-rows: auto 1fr;
    grid-template-columns: auto 256px;
    justify-content: center;
    gap: 40px;
    display: grid;
    position: relative;
  }

  #surfer-container {
    grid-area: 1 / 1 / span 2;
    place-self: end;
    display: inline;
  }

  #sliders {
    flex-direction: column;
    grid-area: 1 / 2;
    gap: 30px;
    display: flex;
  }

  #formula-wrapper {
    flex-direction: column;
    flex-grow: 10;
    grid-area: 2 / 2;
    gap: 10px;
    display: flex;
  }
}

#formula {
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  resize: none;
  background-color: #d3d3d3;
  border: none;
  border-radius: 1ex;
  flex-grow: 10;
  padding: 1.5ex;
  font-size: larger;
}

#go {
  width: 100%;
  background-color: #d3d3d3;
  border: none;
  border-radius: 1ex;
  padding: 1.5ex;
}

#go span {
  font-size: 2rem;
}

